body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.my-name-styles{
    border: 0.3rem solid rgba(108, 122, 137, 1);
    margin: 3rem auto 0.5rem auto;
    padding: 0.3rem;
    width: 30rem;
    font-size: 1.5rem;
}

.my-picture{
    margin-top: 1rem;
    height: 170px;
    width: 170px;
    border-radius: 50%;
}
@media only screen and (max-width: 650px) {
    .my-name-styles{
        width: 25rem;
    }
}

@media only screen and (max-width: 600px) {
    .my-name-styles{
        width: 25rem;
    }
}

@media only screen and (max-width: 450px) {
    .my-name-styles{
        width: 20rem;
    }
}

@media only screen and (max-width: 350px) {
    .my-name-styles{
        width: 17rem;
    }
}

.w3-container{
    width: 70%;
    margin: auto;
}

.w3-half{
    margin-top: 4rem;
    margin-bottom: 4rem;
    padding: 0 3rem 0rem 3rem;
}

.left-half{
    text-align: right;
}

.right-half{
    border-left: 3px solid black;
}

.section-heading{
    font-weight: bold;
    font-size: 1.3rem;
}

.subsection-heading{
    margin-top: 1rem;
    font-weight: bold;
    color: rgba(108, 122, 137, 1);
}

.subsection-content{
    margin-top: 0.2rem;
    color: rgba(108, 122, 137, 1);
}

.section-heading-margin{
    margin-top: 2rem;
}

@media only screen and (max-width: 1100px) {
    .w3-container{
        width: 80%;
    }
}

@media only screen and (max-width: 1000px) {
    .w3-container{
        width: 90%;
    }
}

@media only screen and (max-width: 930px) {
    .w3-container{
        width: 100%;
    }
}

@media only screen and (max-width: 600px) {
    .left-half{
        margin-top: 4rem!important;
        text-align: center;
        border-bottom: 3px solid black;
    }

    .right-half{
        border-left:0px solid black;
        text-align: center;
    }

    .w3-half{
        margin-top: 0rem;
    }

    .w3-container{
        width: 100%;
    }

}

.social-links-name{
    font-weight: bold;
    color: rgba(189, 195, 199, 1);
}

a{
    text-decoration: none;
}
.cloud-heading{
    font-weight: bold;
    color: rgba(189, 195, 199, 1);
}
.about-me-container{
    padding: 3rem;
    background: rgba(241, 169, 160, 0.7);
    color: white;
}

@media only screen and (max-width: 600px) {
    .about-me-container{
        padding: 3rem 0.5rem 3rem 0.5rem;
    }
}
.about-me-heading{
    font-size: 2rem;
    font-weight: bold;
}

.about-me-paragraph{
    width: 70%;
    margin: auto;
    font-weight: bold;
}

hr {
    width: 300px;
    margin-left: auto!important;
    margin-right: auto!important;
    height: 1.5px!important;
    background-color: white;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.top-border{
    border-top: 5px solid rgba(241, 169, 160, 0.7);
}

.website-images{
    width:70%;
    margin: auto;
}

.picture-size{
    border-radius: 50%;
    height: 200px;
    width: 200px;
}

@media only screen and (max-width: 450px) {
    li {
        width: 95%;
        margin: auto auto auto auto;
    }
}
.alternate-background-one{
    background-color: rgba(236, 236, 236, 0.5)
}

.alternate-background-two{
    background-color: rgba(218, 223, 225, 0.5);
}

.project-container{
    padding: 3rem 0 3rem 0;
}

.bottom-border{
    border-bottom: 5px solid rgba(241, 169, 160, 0.7);
}

.project-name{
    color: rgba(46, 49, 49, 1);
}

.project-description-container{
    width: 80%;
    margin: auto;
    color: rgba(108, 122, 137, 1);
}

.website-link-styles{
    color: white!important;
    background-color: rgba(241, 169, 160, 0.7) !important;
    text-decoration: none;
    margin-bottom: 2rem;
    border: 3px solid white!important;
    font-size: 1.5rem;
    width: 20rem;
}

h1{
    margin: 0px!important;
}

.icon-style-projects-page{
    color: rgba(241, 169, 160, 0.7);
    margin-right: 5px;
}

.bullet-point-style{
    margin: 3% 2rem 3% 2rem;
}

.website-points{
    width: 60%;
    margin: auto;
}
.contact-page-container{
    background: rgba(241, 169, 160, 0.7);
    padding: 8rem 0 5rem 0;
}

.content-container{
    background: rgba(236, 240, 241, 1);
    padding: 1rem!important;
    width: 60%;
}

.w3-half-custom {
     margin-top: 0rem!important;
     padding: 0 0rem 0 0rem;
}

.picture-style{
    height: 100%;
    width: 100%;
}

.my-name{
    font-size: 1.7rem;
    color: rgba(241, 169, 160, 0.7);
}

.hr-in-contact-page{
    width: 150px;
    background-color: rgba(241, 169, 160, 0.7)!important;
}

.contact-details{
    color: rgba(108, 122, 137, 1);
}

.icon-style{
     margin-top: 2rem;
     font-size: 2rem;
     color: rgba(241, 169, 160, 0.7);
 }
@media only screen and (max-width: 1280px) {
    .icon-style{
        margin-top: 1.6rem;
    }
}

@media only screen and (max-width: 1250px) {
    .icon-style{
        margin-top: 1.2rem;
    }
}

@media only screen and (max-width: 1200px) {
    .icon-style{
        margin-top: 0.8rem;
    }
}

@media only screen and (max-width: 1140px) {
    .icon-style{
        margin-top: 0.4rem;
    }
}

@media only screen and (max-width: 1080px) {
    .my-name{
        font-size: 1.5rem;
    }

    .icon-style{
        margin-top: 0rem;
        font-size: 1.5rem;
    }
}

@media only screen and (max-width: 992px) {
    .my-name {
        margin-top: 2rem;
        font-size: 2rem;
    }

    .icon-style {
        font-size: 2rem;
        color: rgba(241, 169, 160, 0.7);
    }
}

@media only screen and (max-width: 510px) {
    .contact-page-container{
        padding: 0 0 0 0;
    }

    .content-container{
        padding: 1rem!important;
        width: 100%;
    }

    .icon-style{
        font-size: 2rem;
    }
}
